.container {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    background-color: rgb(195, 0, 255);
    background: linear-gradient(to right, #9900ff, #cc80ff);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .innerBox {
    min-width: 400px;
    height: fit-content;
    width: fit-content;
    background-color: #fff;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    padding: 30px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    flex-direction: column;
    gap: 30px;
  }
  
  .footer{
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .footer button{
    outline: none;
    border: none;
    background-color: #9900ff;
    color: #fff;
    border-radius:5px;
    -webkit-border-radius:5px;
    -moz-border-radius:5px;
    -ms-border-radius:5px;
    -o-border-radius:5px;
    font-weight: bold;
    padding: 10px 16px;
    font-size: 1rem;
    width: 100%;
    margin-top: 2rem;
  }
  
  .footer button:hover{
    background-color: #aa2aff;
  }
  .footer button:disabled{
    background-color: gray;
  }
  
  .footer p{
    font-weight: 700;
    color: #000;
  }
  
  .footer p span a{
    font-weight: bold;
   color: #9900ff;
   letter-spacing: 1px;
   font-size: 1rem;
   text-decoration: none;
  }
  
  
  .footer .error{
    font-weight: bold;
    font-size: 0.875rem;
    color: #ff3300;
    text-align: center;

  }